import React from "react"
import { MdLogout } from 'react-icons/md'
import * as logoutButtonStyle from "./logoutbutton.module.css"
import util from "../../js/util"

const LogoutButton = () => {
    const onClickHandler = () => {
        fetch("/api/do_logout", util.addCsrfHeader({
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            }
        })).then(response => {
            if(response.status === 200) {
                util.navigateToPage("/")
            }
        })
    }

    return (
        <div className={ logoutButtonStyle.loginWrapper } onClick={ onClickHandler }>
            <p><span className={ logoutButtonStyle.loginIcon }><MdLogout /></span><span className={ logoutButtonStyle.loginTitle }> Logout</span></p>
        </div>
    )
}

export default LogoutButton